
import { defineComponent, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { GlobalDataProps } from '../interfaces'
import ColumnList from '../components/ColumnList.vue'
import useLoadMore from '@/tools/useLoadMore'
export default defineComponent({
  name: 'MyHome',
  components: {
    ColumnList
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    // 获取响应式的对象要用计算属性
    const testData = computed(() => store.getters.getColumns) // 专栏
    const total = computed(() => store.state.columns.total) // 总专栏数
    const currentPage = computed(() => store.state.columns.currentPage) // 当前页
    // 用于加载更多的回调
    const { loadMorePage, isLastPage } = useLoadMore(
      'fetchColumns',
      total,
      {
        // 如果当前页是第0页则参数为2
        currentPage: (currentPage.value ? currentPage.value + 1 : 2),
        pageSize: 3
      })

    onMounted(() => {
      // 调用action
      store.dispatch('fetchColumns', { pageSize: 3 })
    })

    return {
      // 键值对形式传递props，testData为一个ColumnProps数组
      testData,
      loadMorePage,
      isLastPage
    }
  }

})
