
import { addColumnAvatar } from '@/tools/helper'
import { defineComponent, PropType, computed } from 'vue'
import { ColumnProps } from '../interfaces'

export default defineComponent({
  name: 'ColumnList', // 首页的专栏列表
  props: {
    // vue官方要求的prop校验
    list: {
      type: Array as PropType<ColumnProps[]>,
      required: true
    }
  },
  setup (props) {
    // 计算属性设置头像
    // 计算属性的作用就是动态监听我们的props属性，如果不使用计算属性则在后面当数据发生变化的时候，界面不能更新
    const computeAvator = computed(() => {
      // map返回的是一个数组，应该把这个响应式数据返回出去
      return props.list.map((column) => {
        // 调整头图大小
        addColumnAvatar(column, 48, 48)
        return column
      })
    })
    return {
      computeAvator
    }
  }
})
