
import { defineComponent, ref, watch } from 'vue'
import useClickOutside from '../hooks/useClickOutside'
export default defineComponent({
  name: 'DropDown',
  props: {
    // title作用：父组件传递给下拉菜单的信息
    title: {
      type: String,
      required: true
    }
  },
  setup () {
    // 创建响应式变量 isOpen含义为下拉框是否开启
    const isOpen = ref(false)
    // 获取ref="dropdownRef"对应结点，因为setup执行时还未渲染，所以此时dropdownRef还是null
    const dropdownRef = ref<null | HTMLElement>(null)

    // 从我们抽象的hooks返回的isClickOutside可以看出是一个泛型为Boolean的响应式变量
    // isClickOutside 含义为是否点击所给元素外面
    const isClickOutside = useClickOutside(dropdownRef)

    // watch的第一个参数在这种情况下需要是一个响应式式对象
    watch(isClickOutside, async () => {
      // 下拉框打开并且点击元素外面
      if (isOpen.value && isClickOutside.value) {
        isOpen.value = false
      }
    })

    // 下拉框切换事件回调函数
    const toggleClick = () => {
      isOpen.value = !isOpen.value
    }

    return {
      isOpen,
      toggleClick,
      dropdownRef
    }
  }

})
