
// 验证邮箱的正则表达式
import { defineComponent, reactive, PropType, onMounted, computed } from 'vue'
import { emitter, emitterForClear } from './ValidateForm.vue'
const emailReg = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
export type TagType = 'input' | 'textarea'

interface IRange {
  message: string;
  length: number;
}

interface RuleProp {
  // 以字面量作为特殊的类型，并使用联合类型
  type: 'required' | 'email' | 'range' | 'password' | 'custom';
  customValidator?: () => boolean; // 自定义验证规则
  message?: string;
  max?: IRange;
  min?: IRange
}
export type RulesProp = RuleProp[]
export default defineComponent({
  props: {
    rules: Array as PropType<RulesProp>,
    modelValue: String,
    tag: {
      type: String as PropType<TagType>, // JS:String -> 字面量type类型
      default: 'input'
    }
  },
  // 关闭透传Attributes
  inheritAttrs: false,
  setup (props, context) {
    // 响应式input对象
    const inputRef = reactive({
      val: computed({
        get: () => props.modelValue || '',
        // 原本的model会触发如下的事件，因为是常事修改计算属性
        set: val => {
          context.emit('update:modelValue', val)
        }
      }),
      error: false,
      message: ''
    })

    // 验证回调
    const validateInput = () => {
      if (props.rules) {
        // every返回布尔值，必须所有元素都返回true结果才为真
        // 当有一个元素计算结果为false时，则立即返回
        const allPassed = props.rules.every((rule) => {
          let passed = true
          const length = inputRef.val.trim().length
          switch (rule.type) {
            case 'email':
              passed = emailReg.test(inputRef.val)
              if (rule.message) {
                inputRef.message = rule.message
              }
              break
            case 'required':
              passed = (inputRef.val.trim() !== '')
              if (rule.message) {
                inputRef.message = rule.message
              }
              break
            case 'range':
              // 存在最大长度限制
              if (rule.max) {
                passed = length <= rule.max.length
                inputRef.message = rule.max.message
              }
              // 存在最小长度限制
              if (rule.min) {
                passed = length >= rule.min.length
                inputRef.message = rule.min.message
              }
              break
            case 'custom':
              passed = rule.customValidator ? rule.customValidator() : true
              break
            default:
              break
          }
          return passed
        })
        inputRef.error = !allPassed
        return allPassed
      }
      return true
    }

    // 清除回调
    const clearValidateInput = () => {
      inputRef.val = ''
    }

    onMounted(() => {
      // 在每个组件渲染时就触发事件
      emitter.emit('form-item-created', validateInput)
      emitterForClear.emit('form-item-clear', clearValidateInput)
    })
    return {
      inputRef,
      validateInput
    }
  }
})
