import { ColumnProps, UserProps, ImageProps } from '../interfaces'

//  入参为Image的Props
export function generateFitUrl (data: ImageProps, width: number, height: number, format = ['m_pad']) {
  if (data && data.url) {
    // reduce：将format中所有的样式组合在一起
    const formatStr = format.reduce((prev, current) => {
      return current + ',' + prev
    }, '')
    data.fitUrl = data.url + `?x-oss-process=image/resize,${formatStr}h_${height},w_${width}`
  }
}

// 用于检查上传文章类型是否正确
interface CheckCondition {
  format?: string[];
  size?:number
}

// 文章上传错误类型
type ErrorType = 'size' | 'format' | null
// 检查文件格式和大小
export function beforeUploadCheck (file: File, condition: CheckCondition) {
  const { format, size } = condition
  let isValidFormat = false
  let isValidSize = false
  if (format && format.includes(file.type)) {
    isValidFormat = true
  }
  if (size && (file.size / 1024 / 1024) < size) {
    isValidSize = true
  }
  let error: ErrorType = null
  if (!isValidFormat) {
    error = 'format'
  }
  if (!isValidSize) {
    error = 'size'
  }
  return {
    passed: isValidFormat && isValidSize,
    error
  }
}

// 入参是一个column或者user的props
export function addColumnAvatar (data: ColumnProps | UserProps, width: number, height: number) {
  // ColumnProps 和 UserProps 都有 avatar这个属性
  if (data.avatar) {
    generateFitUrl(data.avatar, width, height)
  } else {
    // 将data随便视为其中一个
    const parseCol = data as ColumnProps
    data.avatar = {
      // 如果有title说明是column，否则就是user
      fitUrl: require(parseCol.title ? '@/assets/column.jpg' : '@/assets/avatar.jpg')
    }
  }
}

interface TestProps {
  _id:string;
  name:string
}

// TestData数组
const testData: TestProps[] = [{ _id: '1', name: 'a' }, { _id: '2', name: 'b' }]
// 索引类型，需要标注索引类型（如同像数组那样通过索引索取获取值，故称为索引类型）
const testData2:{[key:string]:TestProps} = {
  1: { _id: '1', name: 'a' },
  2: { _id: '2', name: 'b' }
}

// arr -> obj,using reduce
// 约束泛型，T至少要包含有{ _id: string }这个属性
export const arrToObj = <T extends { _id?: string }>(arr: Array<T>) => {
  return arr.reduce((prev, current) => {
    if (current._id) {
      prev[current._id] = current
    } return prev
    // 在reduce归并的最开始prev为空对象，需要规定这个空对象的类型为一个Indexable type
  }, {} as { [key: string]: T })
}
// obj -> arr, using map
export const objToArr = <T>(obj:{[key: string]: T}) => {
  const keysArr = Object.keys(obj)
  const result = keysArr.map(key => obj[key])
  return result
}
