
import 'bootstrap/dist/css/bootstrap.min.css'
import { defineComponent, computed, onMounted, watch } from 'vue'
import { useStore } from 'vuex'
import axios from 'axios'
import { GlobalDataProps } from './interfaces'
import GlobalHeader from './views/GlobalHeader.vue'
import GlobalFooter from './views/GlobalFooter.vue'
import MyLoader from './components/MyLoader.vue'
import createMessage from './components/createMessage'

export default defineComponent({
  name: 'App',
  components: {
    GlobalHeader,
    GlobalFooter,
    MyLoader
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    // 从store中拿到的数据需要计算属性
    const currentUser = computed(() => store.state.user)
    const isLoading = computed(() => store.state.loading)
    const token = computed(() => store.state.token)
    const error = computed(() => store.state.error)
    onMounted(() => {
      // 打开页面,如果当前用户未登录但是浏览器中保存过token
      if (!currentUser.value.isLogin && token.value) {
        // 则修改axios默认配置，加上token
        axios.defaults.headers.common.Authorization = `Bearer ${token.value}`
        // 尝试获取用户信息
        store.dispatch('fetchCurrentUser')
      }
    })
    // watch监听error中的状态，如果发生错误则使用「函数组件」将Message组件动态挂载
    watch(() => error.value.status, () => {
      const { status, message } = error.value
      // 发送错误时
      if (status && message) {
        createMessage(message, 'error')
      }
    })
    return {
      currentUser,
      isLoading,
      error
    }
  }
})
