import { createStore, Commit } from 'vuex'
import axios, { AxiosRequestConfig } from 'axios'
import { GlobalDataProps, GlobalErrorProps } from './interfaces'
import { arrToObj, objToArr } from './tools/helper'

// 封装actions中异步获取数据方法， Commit是actions中commit方法类型，返回值都是get或post得到的数据
const getAndCommit = async (url: string, mutationName: string, commit: Commit) => {
  const resp = await axios.get(url)
  commit(mutationName, resp)
  return resp
}
const postAndCommit = async (url: string, mutationName: string, commit: Commit, payload: any) => {
  const { data } = await axios.post(url, payload) // payload应该是一个对象 详见 https://www.axios-http.cn/docs/post_example
  commit(mutationName, data)
  return data // 还将登录的数据返回
}
const asyncAndCommit = async (url: string,
  mutationName: string,
  commit: Commit,
  config: AxiosRequestConfig = { method: 'get' }, // 默认方法为get
  extraData?: any
) => {
  const { data } = await axios(url, config)
  if (extraData) {
    commit(mutationName, { data, extraData })
  } else {
    commit(mutationName, data)
    return data
  }
}

const store = createStore<GlobalDataProps>({
  // ===========================state===========================
  state: {
    token: localStorage.getItem('token') || '', // 每次打开应用时就会自动尝试获取
    error: { status: false },
    loading: false,
    columns: { data: {}, currentPage: 0, total: 0 },
    posts: { data: {}, loadedColumns: [] },
    user: { isLogin: false } // 初始假设用户没有登录
  },
  // ===========================mutation===========================
  mutations: {
    createPost (state, newPost) {
      state.posts.data[newPost.data._id] = newPost.data
    },
    fetchColumns (state, resp) {
      const { data } = state.columns
      const { list, count, currentPage } = resp.data// count为column总数 list为数据
      state.columns = {
        data: { ...data, ...arrToObj(list) },
        total: count, // 总专栏数
        currentPage: currentPage * 1 // string -> number
      }
    },
    fetchColumn (state, resp) {
      // 索引类型：设置data中一个column信息
      state.columns.data[resp.data.data._id] = resp.data.data
    },
    fetchPosts (state, { data: rawData, extraData: columnId }) {
      state.posts.data = { ...state.posts.data, ...arrToObj(rawData.data.list) }
      state.posts.loadedColumns.push(columnId)
    },
    fetchPost (state, rawData) {
      // 查询单个帖子
      state.posts.data[rawData.data.data._id] = rawData.data.data
    },
    setLoading (state, flag) {
      state.loading = flag
    },
    setError (state, e: GlobalErrorProps) {
      state.error = e
    },
    // 获取当前用户信息
    fetchCurrentUser (state, rawData) {
      state.user = { isLogin: true, ...rawData.data.data }
    },
    // 更新文章后，使用map操作让state中对应的post变更为data，也就是返回的新的帖子
    updatePost (state, { data }) {
      state.posts.data[data._id] = data
    },
    deletePost (state, { data }) {
      delete state.posts.data[data._id]
    },
    login (state, rawData) {
      const token = rawData.data.token
      state.token = token
      // 登录之后修改state中和接下来axios中的token
      localStorage.setItem('token', token)
      axios.defaults.headers.common.Authorization = `Bearer ${token}`
    },
    logout (state) {
      state.token = ''
      localStorage.removeItem('token')
      state.user = { isLogin: false }
      // delete操作符 删除对象中某一属性
      delete axios.defaults.headers.common.Authorization
    }
  },
  // ===========================actions用于异步地获取数据===================
  actions: {
    // 普通的promise版本
    // fetchColumns (context) {
    //   axios.get('/columns').then((resp) => {
    //     context.commit('fetchColumns', resp)
    //   })
    // },
    async fetchColumns (context, params = {}) {
      // 默认值 + 解构
      const { currentPage = 1, pageSize = 3 } = params
      // 比较state中的currentPage和传入的params的currentPage大小
      if (context.state.columns.currentPage < currentPage) {
        return asyncAndCommit(`/columns?currentPage=${currentPage}&pageSize=${pageSize}`, 'fetchColumns', context.commit)
      }
    },
    fetchColumn (context, cid) {
      if (!context.state.columns.data[cid]) {
        return getAndCommit(`/columns/${cid}`, 'fetchColumn', context.commit)
      }
    },
    fetchPosts (context, cid) {
      // 一组posts和一个columnId绑定，如果loadedColumns中没有对应的cid，说明这一组Posts肯定没有fetch
      if (!context.state.posts.loadedColumns.includes(cid)) {
        return asyncAndCommit(`/columns/${cid}/posts`, 'fetchPosts', context.commit, { method: 'get' }, cid)
      }
    },
    fetchPost (context, id) {
      const currentPost = context.state.posts.data[id]
      // 当前post存在并且正文content也要存在，因为有可能是省略版的post内容
      if (!currentPost || !currentPost.content) {
        return getAndCommit(`/posts/${id}`, 'fetchPost', context.commit)
      } else {
        return Promise.resolve({ data: currentPost })
      }
    },
    login (context, payload) {
      return postAndCommit('/user/login', 'login', context.commit, payload)
    },
    fetchCurrentUser (context) {
      const rawData = getAndCommit('/user/current', 'fetchCurrentUser', context.commit)
      return rawData
    },
    createPost (context, payload) {
      return postAndCommit('/posts', 'createPost', context.commit, payload)
    },
    updatePost (context, { id, payload }) {
      return asyncAndCommit(`/posts/${id}`, 'updatePost', context.commit, { method: 'patch', data: payload })
    },
    deletePost ({ commit }, id) {
      return asyncAndCommit(`/posts/${id}`, 'deletePost', commit, { method: 'delete' })
    },
    // 将上面两个login和fetchCurrentUser合并在一起，先执行登录再获取当前用户信息
    async loginAndFetch (context, loginData) {
      return await context.dispatch('login', loginData).then(() => {
        return context.dispatch('fetchCurrentUser')
      })
    }
  },
  // ===========================getter获取数据===========================
  getters: {
    // 通过id返回对应专栏
    getColumnById (state) {
      return (id: string) => {
        // 通过返回函数的形式实现带参数的getter
        return state.columns.data[id]
      }
    },
    // 通过专栏id返回对应的帖子
    getPostsByCid (state) {
      return (cid: string) => {
        return objToArr(state.posts.data).filter(post => post.column === cid)
      }
    },
    // 通过帖子id返回帖子
    getCurrentPost (state) {
      return (id: string) => {
        return state.posts.data[id]
      }
    },
    getColumns (state) {
      return objToArr(state.columns.data)
    }
  }
})

export default store
