
import { defineComponent, PropType, ref, onMounted, onUnmounted, watch } from 'vue'
import EasyMDE, { Options } from 'easymde'

export default defineComponent({
  name: 'myEditor',
  props: {
    modelValue: String,
    options: Object as PropType<Options>
  },
  emits: ['update:modelValue', 'change', 'blur'],
  setup (props, context) {
    // 拿到响应式的元素对象
    const textArea = ref<null | HTMLTextAreaElement>(null)
    let easyMDEInstance: EasyMDE | null = null
    // 挂载完毕后再传递属性，这里是习惯，不会直接操作pops的属性值
    const innerValue = ref(props.modelValue || '')

    onMounted(() => {
      if (textArea.value) {
        const config: Options = {
          ...(props.options || {}), // 用于外部传递的options
          element: textArea.value, // element元素，options中需要
          initialValue: innerValue.value // 初始值，options中需要
        }
        easyMDEInstance = new EasyMDE(config)
        // 监控change事件
        easyMDEInstance.codemirror.on('change', () => {
          if (easyMDEInstance) {
            // 拿到当前的值
            const updatedValue = easyMDEInstance.value()
            innerValue.value = updatedValue
            context.emit('update:modelValue', updatedValue) // 组件上的 v-model
          }
        })
        // 监控blur事件
        easyMDEInstance.codemirror.on('blur', () => {
          if (easyMDEInstance) {
            context.emit('blur')
          }
        })
      }
    })

    // watch侦听响应式变化
    watch(() => props.modelValue, (newValue) => {
      // easyMDE如果已经被实例化
      if (easyMDEInstance) {
        console.log(1)
        if (newValue !== innerValue.value) {
          console.log(2)
          easyMDEInstance.value(newValue || '')
        }
      }
    })

    // 销毁对应的实例
    onUnmounted(() => {
      if (easyMDEInstance) {
        easyMDEInstance.cleanup()
      }
      easyMDEInstance = null
    })

    // 清空编辑区内容方法
    const clear = () => {
      if (easyMDEInstance) {
        easyMDEInstance.value('')
      }
    }
    const getMDEInstance = () => {
      return easyMDEInstance
    }
    return {
      textArea,
      clear,
      getMDEInstance
    }
  }
})
