// 框架与生态
import { createApp } from 'vue'
import router from './router'
import store from './store'
import axios from 'axios'
// 根组件
import App from './App.vue' // vuex

// EasyMDE 样式
import 'easymde/dist/easymde.min.css'

const app = createApp(App)
app.use(router) // vue-router
app.use(store)

// axios
axios.defaults.baseURL = 'http://apis.imooc.com/api/'
// axios请求设置
axios.interceptors.request.use(config => {
  // get 请求，添加到 url 中
  config.params = { ...config.params, icode: 'DF4CC527C9BF4F61' }
  // 其他请求，添加到 body 中， 如果是上传表单或文件，添加到 FormData 中
  if (config.data instanceof FormData) {
    config.data.append('icode', 'DF4CC527C9BF4F61')
  } else {
  // 普通的 body 对象，添加到 data 中
    config.data = { ...config.data, icode: 'DF4CC527C9BF4F61' }
  }

  store.commit('setLoading', true)
  store.commit('setError', { status: false, message: '' })
  return config
})
// axios响应设置
axios.interceptors.response.use(config => {
  store.commit('setLoading', false)
  return config
}, e => {
  // axios返回错误情况，error就是错误的信息
  let error = null
  // 两种不同的报错信息
  if (e.response) {
    error = e.response.data.error
    store.commit('setError', { status: true, message: error })
    store.commit('setLoading', false)
    // 返回promise给catch
    return Promise.reject(e.response.data)
  } else {
    error = e.message
    store.commit('setError', { status: true, message: error })
    store.commit('setLoading', false)
    // 返回promise给catch
    return Promise.reject(error)
  }
})

app.mount('#app')
