
import { defineComponent, onUnmounted } from 'vue'
import mitt from 'mitt'
type ValidateFunc = () => boolean
type ValidateClear = () => void
// mitt3.0以上要求事件名称要和事件类型一一对应
// 类型别名 type 表名这种类型必须是一个对象，且这个对象的键值对如下
// 要求emitter fire时事件入参必须是ValidateFunc
type Events = {
  'form-item-created': ValidateFunc
}
type EventsClear = {
    'form-item-clear': ValidateClear
}
export const emitter = mitt<Events>()
export const emitterForClear = mitt<EventsClear>()
// ValidateInput.vue组件中的验证回调函数进行类型声明

export default defineComponent({
  emits: ['form-submit'],
  setup (props, context) {
    // 表单验证逻辑
    let funcArr: ValidateFunc[] = []
    const callback = (func: ValidateFunc) => {
      funcArr.push(func)
    }

    // 表单清除逻辑
    let funcArrClear: ValidateClear[] = []
    const callbackForClear = (func: ValidateClear) => {
      funcArrClear.push(func)
    }

    // 注册emitt事件
    emitter.on('form-item-created', callback)
    emitterForClear.on('form-item-clear', callbackForClear)

    // form表单提交回调函数
    const submitForm = () => {
      // func[] -> boolean[] -> boolean
      const result = funcArr.map(func => func()).every(flag => flag)
      // 触发父组件的form-submit事件
      context.emit('form-submit', result)
      if (result) {
        funcArrClear.map(func => func())
      }
    }
    onUnmounted(() => {
      emitter.off('form-item-created', callback)
      emitterForClear.off('form-item-clear', callbackForClear)
      funcArr = []
      funcArrClear = []
    })
    return {
      submitForm
    }
  }

})
