
import { defineComponent, ref, PropType, watch } from 'vue'
import axios from 'axios'
// 字面量类型：文件上传的状态,ready可以上传，loading正在上传，success上传成功，error上传失败
type UploadStatus = 'ready' | 'loading' | 'success' | 'error'
// 函数类型，作用是检查上传的图片是否符合要求
type CheckFunction = (file: File) => boolean;
export default defineComponent({
  props: {
    // 文件上传路径
    action: {
      type: String,
      required: true
    },
    // 上传验证规则
    beforeUpload: {
      type: Function as PropType<CheckFunction>
    },
    uploaded: {
      type: Object
    }
  },
  // 触发父组件的两个事件
  emits: ['file-uploaded', 'file-uploaded-error'],
  inheritAttrs: false,
  setup (props, context) {
    const fileInput = ref<null | HTMLInputElement>(null) // 通过ref拿到inputt结点
    const fileStatus = ref<UploadStatus>(props.uploaded ? 'success' : 'ready') // 根据传入的属性创建响应式状态变量
    const uploadedData = ref(props.uploaded) // 作用域插槽，传递给父组件的响应式变量，并且根据情况来进行回显
    // 监听 props.uploaded 原因是需要fileStatus和uploadedData跟随传入的uploaded变化而变化
    watch(() => { return props.uploaded }, (newValue) => {
      if (newValue) {
        fileStatus.value = 'success'
        uploadedData.value = newValue
      }
    })

    // 文件Input框触发回调
    const triggerUpload = () => {
      if (fileInput.value) {
        // 触发Input文件选择框的点击事件
        fileInput.value.click()
      }
    }
    // InputFile文件候选框，监听选择了文件后的回调
    const handleFileChange = (e: Event) => {
      const currentTarget = e.target as HTMLInputElement
      // 如果当前input的时间选择了文件（currentTarget.files不为空）
      if (currentTarget.files) {
        const files = Array.from(currentTarget.files)
        // 对头图进行类型和大小的检查
        if (props.beforeUpload) {
          const result = props.beforeUpload(files[0])
          // 图片不符合要求则直接返回，不上传文件
          if (!result) {
            return
          }
        }
        // 头图符合要求则往下执行
        fileStatus.value = 'loading'
        const formData = new FormData()
        formData.append('file', files[0])
        axios.post(props.action, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(resp => {
          fileStatus.value = 'success'
          uploadedData.value = resp.data
          context.emit('file-uploaded', resp.data)
        }).catch((error) => {
          fileStatus.value = 'error'
          context.emit('file-uploaded-error', error)
        }).finally(() => {
          // fileInput.value表示input文件结点
          if (fileInput.value) {
            fileInput.value.value = ''
          }
        })
      }
    }
    return {
      fileInput,
      triggerUpload,
      fileStatus,
      handleFileChange,
      uploadedData
    }
  }
})
