
// 框架对象
import { defineComponent, ref, onMounted, reactive } from 'vue'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import EasyMDE, { Options } from 'easymde'
// Component组件
import ValidateForm from '../components/ValidateForm.vue'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import MyUploader from '../components/MyUploader.vue'
import createMessage from '../components/createMessage'
import MyEditor from '@/components/MyEditor.vue'
// 类型
import { PostProps, ImageProps, GlobalDataProps, ResponseType } from '../interfaces'
// 函数
import { beforeUploadCheck } from '../tools/helper'

// easyMED组件对象
interface EditorInstance {
  clear: ()=>void;
  getMDEInstance: () => EasyMDE | null;
}

export default defineComponent({
  name: 'CreatePost',
  components: {
    ValidateForm,
    ValidateInput,
    MyUploader,
    MyEditor
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const store = useStore<GlobalDataProps>()

    const uploadedData = ref()
    const editorRef = ref<null | EditorInstance>(null) // 拿出子组件
    const editorStatus = reactive({
      isValid: true,
      message: ''
    })
    const isEditMode = !!route.query.id // 快速取得boolean值

    const editorOptions: Options = {
      spellChecker: false
    }
    // 编辑帖子还是创建帖子
    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData) => {
          // fetch拿到的纯数据resp，需要进一步拿到出数据
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedData.value = { data: currentPost.image }
          }
          titleVal.value = currentPost.title
          contentVal.value = currentPost.content || ''
        })
      }
    })

    // 头图相关
    let imageId = '' // 如果有头图才会赋值imageId，并且是一个对象
    // 用于头图上传后将头图信息赋值给imageId变量，用于之后帖子的创建
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }
    // 对头图进行检查的回调
    const uploadCheck = (file: File) => {
      const result = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 2 })
      const { passed, error } = result
      if (error === 'format') {
        createMessage('上传图片只能是 JPG/PNG 格式!', 'error')
      }
      if (error === 'size') {
        createMessage('上传图片大小不能超过 1Mb', 'error')
      }
      return passed
    }

    // 帖子文章相关
    const titleVal = ref('')
    const contentVal = ref('')
    // 文章验证规则
    const titleRules: RulesProp = [
      { type: 'required', message: '文章标题不能为空' }
    ]

    // blur时的验证回调
    const checkEditor = () => {
      if (contentVal.value.trim() === '') {
        editorStatus.isValid = false
        editorStatus.message = '文章详情不能为空'
      } else {
        editorStatus.isValid = true
        editorStatus.message = ''
      }
    }

    // 帖子的头图+文章创建
    const onFormSubmit = (result: boolean) => {
      checkEditor() // 单独为MDE做的检查
      // 标题和markdown的正文都没问题才能通过
      if (result && editorStatus.isValid) {
        const { column, _id } = store.state.user
        if (column) {
          // 文章内容主体
          const newPost: PostProps = {
            title: titleVal.value,
            content: contentVal.value,
            column: column,
            author: _id
          }
          // 是否存在头图
          if (imageId) {
            newPost.image = imageId
          }

          // 是否为更新帖子内容
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const sendData = isEditMode
            ? {
                id: route.query.id,
                payload: newPost
              }
            : newPost

          // 跟新或者创建
          store.dispatch(actionName, sendData).then(() => {
            createMessage('发表成功，2秒后跳转到文章', 'success', 2000)
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }

    return {
      titleRules,
      onFormSubmit,
      titleVal,
      contentVal,
      uploadCheck,
      handleFileUploaded,
      uploadedData,
      isEditMode,
      editorOptions,
      editorRef,
      editorStatus,
      checkEditor
    }
  }
})
